<template>
  <div class="hello">


        <el-form  label-width="100px" class="demo-ruleForm">
          <el-form-item>
            <el-button type="primary" @click="submitForm()">确定</el-button>
          </el-form-item>
        </el-form>



  </div>
</template>

<script>
  import  storage from  '../utils/storage.js'
  import  getUrl from  '../utils/getUrlKey.js'
  export default {
    data() {
      return {
        activeName: 'first',
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },

      submitForm(formName) {
        var queryArr=getUrl.getQuery()
        if(queryArr.hasOwnProperty('line_user_id')){
          var line_user_id=queryArr.line_user_id
        }else{
          var line_user_id=''
        }
        if(queryArr.hasOwnProperty('user_type')){
          var user_type=queryArr.user_type
        }else{
          var user_type='teacher'
        }

          if(queryArr.hasOwnProperty('student_id')){
              var student_id=queryArr.student_id
          }else{
              var student_id=''
          }

        if(user_type=='teacher'){
            var param={
                line_user_id:line_user_id,
                type:'line',
                user_type:user_type
            }
            var url=this.baseUrl+"/api/login"
            this.axios.post(url, param).then((res)=> {
                console.log(res)
                if (res.data.code != 200) {
                    this.$message({
                        message: res.data.msg,
                        type: "error"
                    });
                    return false;
                }else{
                    localStorage.setItem('teach_user_token','Bearer '+res.data.data.access_token)
                    this.$message({
                        message: res.data.msg,
                        type: "success"
                    });
                    this.getTeacherInfo()
                }
            }).catch((error)=> {
                console.log(error);
            });
        }

        if(user_type=='student'){
          var param={
            line_user_id:line_user_id,
            type:'line',
              user_type:user_type,
              student_id:student_id
          }
          var url=this.baseUrl+"/api/login"
          // var url = "http://www.myteach.com/api/login";
          this.axios.post(url, param).then((res)=> {
            console.log(res)
            if (res.data.code != 200) {
              this.$message({
                message: res.data.msg,
                type: "error"
              });
              return false;
            }else{
              localStorage.setItem('student_user_token','Bearer '+res.data.data.access_token)
              this.$message({
                message: res.data.msg,
                type: "success"
              });
              this.getStudentInfo()

            }
          }).catch((error)=> {
            console.log(error);
          });
        }

      },
      getTeacherInfo(){
        var url=this.baseUrl+"/api/user"
        //var url = "http://www.myteach.com/api/user";
        this.axios.get(url).then((res)=> {
          if (res.data.code != 200) {
            return false;
          }else{
            this.userInfo=res.data.data
            storage.set('userinfo',this.userInfo)
            storage.set('role',this.userInfo.role)
            if(this.userInfo.role=='teacher_admin'){
                window.open(this.baseUrl+'/#/teacherAdmins','_self')
            }
            if(this.userInfo.role=='teacher'){
                window.open(this.baseUrl+'/#/teachers','_self')
            }
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
        getStudentInfo(){
            var url=this.baseUrl+"/api/student_user"
            //var url = "http://www.myteach.com/api/user";
            this.axios.get(url).then((res)=> {
                if (res.data.code != 200) {
                    return false;
                }else{
                    this.userInfo=res.data.data
                    storage.set('userinfo',this.userInfo)

                    window.open(this.baseUrl+'/#/students','_self')

                }
            }).catch((error)=> {
                console.log(error);
            });
        },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



