<template>
  <div class="hello">
    <el-tag  @click="openMessageList">你有待收消息:{{show_num}}条，点击查看</el-tag>
    <el-tabs v-model="activeName"  @click="getTeacherData" type="card" @tab-click="handleClick">
      <el-tab-pane label="教师列表" name="first">
        <el-form   :inline="true" class="form-inline" @submit.native.prevent>
          <el-form-item>
            <el-button type="primary"  @click="addTeacher" size="mini">新增</el-button>
          </el-form-item>
        </el-form>

        <el-table
                :data="teacherData"
                style="width: 100%">
          <el-table-column
                  prop="name"
                  label="姓名"
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="name"
                  label="所属学校"
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="created_at"
                  label="加入日期">
          </el-table-column>
        </el-table>

        <div class="block">
          <el-pagination
                  @size-change="handleSizeChangeTeacher"
                  @current-change="handleCurrentChangeTeacher"
                  :current-page="currentPageTeacher"
                  :page-sizes="[10, 50, 100, 500, 1000]"
                  :page-size="pageSizeTeacher"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="pageTotalTeacher">
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="学校列表"  @click="getSchoolData" name="second">
        <el-form   :inline="true" class="form-inline" @submit.native.prevent>
          <el-form-item>
            <el-button type="primary"  @click="addSchool" size="mini">新增</el-button>
          </el-form-item>
        </el-form>
        <el-table
                :data="schoolData"
                style="width: 100%">
          <el-table-column
                  prop="name"
                  label="学校名称"
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="is_admin_agree"
                  label="学校名称"
                  width="180">
            <template slot-scope="scope">

              <el-tag  v-if="scope.row.is_admin_agree==1" >是</el-tag>
              <el-tag  v-if="scope.row.is_admin_agree==0" >否</el-tag>
            </template>
          </el-table-column>
          <el-table-column
                  prop="created_at"
                  label="创建日期">
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
                  @size-change="handleSizeChangeSchool"
                  @current-change="handleCurrentChangeSchool"
                  :current-page="currentPageSchool"
                  :page-sizes="[10, 50, 100, 500, 1000]"
                  :page-size="pageSizeSchool"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="pageTotalSchool">
          </el-pagination>
        </div>
      </el-tab-pane>
      <el-tab-pane label="学生列表"    @click="getStudentData"  name="third">
        <el-form   :inline="true" class="form-inline" @submit.native.prevent>
        <el-form-item>
          <el-button type="primary"  @click="addStudent" size="mini">新增</el-button>
        </el-form-item>
        </el-form>
        <el-table
                :data="studentData"
                style="width: 100%">
          <el-table-column
                  prop="name"
                  label="姓名"
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="school_name"
                  label="所属学校">
          </el-table-column>
          <el-table-column
                  prop="is_follow"
                  label="是否关注我">
            <template slot-scope="scope">

              <el-tag   >{{scope.row.is_follow}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column
                  prop="created_at"
                  label="加入日期">
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination
                  @size-change="handleSizeChangeStudent"
                  @current-change="handleCurrentChangeStudent"
                  :current-page="currentPageStudent"
                  :page-sizes="[10, 50, 100, 500, 1000]"
                  :page-size="pageSizeStudent"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="pageTotalStudent">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>

       <TeacherAdd  ref="teacherAdd"  @func="teacherBack"   ></TeacherAdd>
        <SchoolAdd  ref="schoolAdd"  @func="schoolBack"  ></SchoolAdd>
        <StudentAdd  ref="studentAdd"  @func="studentBack"    ></StudentAdd>
        <MessageList  ref="messageList"></MessageList>
  </div>
</template>

<script>
  import TeacherAdd from './TeacherAdd'
  import SchoolAdd from './SchoolAdd'
  import StudentAdd from './StudentAdd'
  import MessageList from './MessageList'
  import storage from '../utils/storage'

  export default {
    components: {
      // 局部注册 TheSidebar
      TeacherAdd,
      SchoolAdd,
      StudentAdd,
      MessageList
    },
    data() {
      return {
        activeName: 'first',
        pageSizeTeacher:10,
        currentPageTeacher:1,
        pageTotalTeacher:0,
        pageSizeSchool:10,
        currentPageSchool:1,
        pageTotalSchool:0,
        pageSizeStudent:10,
        currentPageStudent:1,
        pageTotalStudent:0,
        teacherData:[],
        schoolData:[],
        studentData:[],
        show_num:0,
        token:'',
        userInfo:[]
      }
    },
    methods: {
      handleSizeChangeTeacher:function(val) {
        this.pageSizeTeacher = val;
        this.getTeacherData();
      },
      handleCurrentChangeTeacher:function(val) {
        this.currentPageTeacher = val;
        this.getTeacherData();
      },
      handleSizeChangeSchool:function(val) {
        this.pageSizeSchool = val;
        this.getSchoolData();
      },
      handleCurrentChangeSchool:function(val) {
        this.currentPageSchool = val;
        this.getSchoolData();
      },
      handleSizeChangeStudent:function(val) {
        this.pageSizeStudent = val;
        this.getStudentData();
      },
      handleCurrentChangeStudent:function(val) {
        this.currentPageStudent = val;
        this.getStudentData();
      },
      handleClick(tab, event) {
        console.log(tab, event);
      },
      addTeacher(){
        this.$refs.teacherAdd.initForm()
      },
      addSchool(){
        this.$refs.schoolAdd.initForm()
      },
      addStudent(){
        this.$refs.studentAdd.initForm()
      },
      openMessageList(){
        var obj={
           send_type:'teacher',
           is_teacher_admin:true
        }
        this.$refs.messageList.initForm(obj)
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var param={
              email:this.ruleForm.email
            }
            var url=this.baseUrl+"/api"
            //var url = "http://www.myteach.com/api";
            this.axios.post(url, param).then((res)=> {
              console.log(res)
              this.loading = false;
              if (res.data.code != 200) {
                this.$message({
                  message: res.data.msg,
                  type: "error"
                });
                return false;
              }
            }).catch((error)=> {
              console.log(error);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      getTeacherData(){
        var param = {
          pageSize: this.pageSizeTeacher,
          page: this.currentPageTeacher,
        };
        var url=this.baseUrl+"/api/getTeacherData"
        //var url = "http://www.myteach.com/api/getTeacherData";
        this.axios.post(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeTeacher=res.data.data.pageSize
            this.pageTotalTeacher = res.data.data.total
            this.teacherData=res.data.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      getSchoolData(){
        var param = {
          pageSize: this.pageSizeSchool,
          page: this.currentPageSchool,
        };
        var url=this.baseUrl+"/api/getSchoolData"
        //var url = "http://www.myteach.com/api/getSchoolData";
        this.axios.post(url, param).then((res)=> {
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeSchool=res.data.data.pageSize
            this.pageTotalSchool = res.data.data.total
            this.schoolData=res.data.data.data
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      getStudentData(){
        var param = {
          pageSize: this.pageSizeStudent,
          page: this.currentPageStudent,
          user_type:'teacher_admin'
        };
        var url=this.baseUrl+"/api/getStudentData"
        //var url = "http://www.myteach.com/api/getStudentData";
        this.axios.post(url, param).then((res)=> {
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeStudent=res.data.data.pageSize
            this.pageTotalStudent = res.data.data.total
            this.studentData=res.data.data.data
            this.studentData.forEach(value => {
              value.school_name=value.school.name
              if(value.follow.length>0){
                value.is_follow='是'
              }else{
                value.is_follow='否'
              }
            })

          }
        }).catch((error)=> {
          console.log(error);
        });
      },

      schoolBack(){
        this.getSchoolData()
      },
      teacherBack(){
        this.getTeacherData()
      },
      studentBack(){
        this.getStudentData()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
        websocketMessage(){
            setTimeout(()=>{
                const  userInfo=storage.get('userinfo')
                //创建一个webSocket 实例
                // var s=location.origin
                // var h=s.substr(0,s.lastIndexOf(":"))
                //var host= location.origin.replace(/^http/, 'ws')
                //   var host= 'wss://myteachceshi.herokuapp.com'
                var host ='ws://127.0.0.1:9000'
                var ws = new WebSocket(host);
                var uid=userInfo.id
                ws.onopen = function(){

                    var params ={
                        uid:uid,
                        type:'teacher'
                    };
                    var data=JSON.stringify(params)
                    ws.send(data);
                };
                ws.onmessage = (e)=>{
                    var res=JSON.parse(e.data);
                    console.log(res.data.show_num);
                    this.show_num=res.data.show_num
                };
            },300)

        }
    },

    mounted() {
      this.getTeacherData()
      this.getSchoolData()
      this.getStudentData()
      this.websocketMessage()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



