// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router'

import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(ElementUI);
Vue.use(VueAxios, axios)

Vue.config.productionTip = false
// Vue.prototype.baseUrl='http://www.myteach.com'
Vue.prototype.baseUrl='https://myteachceshi.herokuapp.com'

//路由导航守卫
router.beforeEach((to, from, next) => {
    //判断其他页面是否有token
    const teach_user_token = localStorage.getItem('teach_user_token');
    const student_user_token = localStorage.getItem('student_user_token');
    if (to.path === '/login'||to.path === '/'||to.path === ''||to.path === '/lineLogin'){
        if(teach_user_token){
            const role=localStorage.getItem('role')
            if(role=='teacher'){
                return next('/teachers')
            }else{
                return next('/teacherAdmins')
            }

        }
        if(student_user_token){
            return next('/students')
        }
       return next()
    }else if(to.path === '/register'||to.path === '/lineBind'){
        return next()
    }
    if(!teach_user_token&&!student_user_token){
       return next('/login')
    }

    return next()

});


axios.interceptors.request.use(

    config => {
      if (localStorage.getItem('teach_user_token')) {
        config.headers.Authorization = localStorage.getItem('teach_user_token');
      }
        if (localStorage.getItem('student_user_token')) {
            config.headers.Authorization = localStorage.getItem('student_user_token');
        }
      return config;
    },
    error => {
      return Promise.reject(error);
    });

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



