
<template>
    <div id="header" >
        <ul >
            <li> <a v-if="!token&&!student_token"  href="/#/register">教师管理员注册</a></li>
            <li> <a v-if="!token&&!student_token"  href="/#/login">登录</a></li>
            <li> <a v-if="token||student_token" href="javascript:;"  @click="logout" >退出</a></li>
            <li> <a v-if="!token&&!student_token"  href="javascript:;"  @click="goToLineLogin" >line登录</a></li>

        </ul>
    </div>
</template>
<script>
    export default {
        name:'TheHeader',
        data(){
            return {
                token:'',
                student_token:''
            }
        },
        methods:{
            logout(){
                //var url = "http://www.myteach.com/api/logout";
                var url=this.baseUrl+"/api/logout"
                this.axios.post(url).then((res)=> {
                    console.log(res)
                    this.loading = false;
                    if (res.data.code != 200) {
                        this.$message({
                            message: res.data.msg,
                            type: "error"
                        });
                        return false;
                    }else{
                        this.$message({
                            message: res.data.msg,
                            type: "success"
                        });
                        localStorage.removeItem('teach_user_token')
                        localStorage.removeItem('student_user_token')
                        window.open(this.baseUrl+'/#/login?xxxx'+Math.random(),'_self')
                    }
                }).catch((error)=> {
                    this.loading = false;
                    console.log(error);
                });
            },
            goToLineMain(){
                window.open(this.baseUrl+'/line','_self')
            },
            goToLineLogin(){
                window.open(this.baseUrl+'/gotoauthpage','_self')
            }
        },
        created() {
            this.token= localStorage.getItem('teach_user_token');
            this.student_token= localStorage.getItem('student_user_token');
        },
        watch: {
            '$route'(to, from) {
                this.$router.go(0);
            }
        },

    }
</script>
<style scoped>
    .title {
        display: none;
    }
    ul{
        list-style: none;
    }
    ul>li{
        margin-bottom: 8px;
    }

    .navbar-default .navbar-nav > .active > a { background: rgba(0,0,0,.03);}
</style>



