<template>
  <div class="hello">
    <el-button  type="primary"  >请输入用户名和密码绑定：</el-button>
    <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
      <el-tab-pane label="教师" name="first">
        <el-form :model="ruleForm1" status-icon :rules="rules1" ref="ruleForm1" label-width="100px" class="demo-ruleForm">
          <el-form-item label="邮箱" prop="email">
            <el-input type="email" v-model="ruleForm1.email" ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="ruleForm1.password" ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm1')">提交</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="学生" name="second">
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
          <el-form-item label="姓名" prop="name">
            <el-input type="name" v-model="ruleForm2.name" ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="ruleForm2.password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')">提交</el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>


  </div>
</template>

<script>
  import  storage from  '../utils/storage.js'
  import  getUrl from  '../utils/getUrlKey.js'
  export default {
    data() {
      return {
        activeName: 'first',
        ruleForm1: {
          email: '',
          password:''
        },
        ruleForm2: {
          name: '',
          password:''
        },
        rules1: {
          email: [
            { required: true, message: '请填写邮箱', trigger: 'change' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'change' }
          ],
        },
        rules2: {
          name: [
            { required: true, message: '请填写姓名', trigger: 'change' }
          ],
          password: [
            { required: true, message: '请填写密码', trigger: 'change' }
          ],
        }
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },

      submitForm(formName) {
        var queryArr=getUrl.getQuery()
        if(queryArr.hasOwnProperty('line_user_id')){
          var line_user_id=queryArr.line_user_id
        }else{
          return
        }
        if(formName=='ruleForm1'){
          this.$refs[formName].validate((valid) => {
            if (valid) {
              var param={
                email:this.ruleForm1.email,
                password:this.ruleForm1.password,
                line_user_id:line_user_id,
                type:'teacher'
              }
              var url=this.baseUrl+"/api/lineBind"
              // var url = "http://www.myteach.com/api/login";
              this.axios.post(url, param).then((res)=> {
                console.log(res)
                if (res.data.code != 200) {
                  this.$message({
                    message: res.data.msg,
                    type: "error"
                  });
                  return false;
                }else{
                  this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  window.open(this.baseUrl+'/line','_self')
                }
              }).catch((error)=> {
                console.log(error);
              });
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }

        if(formName=='ruleForm2'){
          this.$refs[formName].validate((valid) => {
            if (valid) {
              var param={
                name:this.ruleForm2.name,
                password:this.ruleForm2.password,
                line_user_id:line_user_id,
                type:'student'
              }
              var url=this.baseUrl+"/api/lineBind"
              // var url = "http://www.myteach.com/api/login";
              this.axios.post(url, param).then((res)=> {
                console.log(res)
                if (res.data.code != 200) {
                  this.$message({
                    message: res.data.msg,
                    type: "error"
                  });
                  return false;
                }else{
                  this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  window.open(this.baseUrl+'/line','_self')
                }
              }).catch((error)=> {
                console.log(error);
              });
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        }

      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



