<template>
  <div class="hello">
    <el-tag  @click="openMessageList">你有待收消息:{{show_num}}条，点击查看</el-tag>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">

      <el-tab-pane label="教师列表"   name="first">
        <el-table
                :data="teacherData"
                style="width: 100%">
          <el-table-column
                  prop="name"
                  label="姓名"
                  width="180">
          </el-table-column>
          <el-table-column
                  prop="is_follow"
                  label="我的关注">
            <template slot-scope="scope">

              <el-tag   >{{scope.row.is_follow}}</el-tag>
            </template>
          </el-table-column>

          <el-table-column
                  prop="created_at"
                  label="加入日期">
          </el-table-column>
          <el-table-column
                  prop="is_follow"
                  label="操作">
            <template slot-scope="scope">

              <el-tag @click="follow(scope.row,0)"  style="cursor: pointer" v-if="scope.row.is_follow=='是'"  >取关</el-tag>
              <el-tag  @click="follow(scope.row,1)" style="cursor: pointer" v-if="scope.row.is_follow=='否'"  >关注</el-tag>
            </template>
          </el-table-column>
        </el-table>

        <div class="block">
          <el-pagination
                  @size-change="handleSizeChangeTeacher"
                  @current-change="handleCurrentChangeTeacher"
                  :current-page="currentPageStudent"
                  :page-sizes="[10, 50, 100, 500, 1000]"
                  :page-size="pageSizeStudent"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="pageTotalStudent">
          </el-pagination>
        </div>
      </el-tab-pane>
    </el-tabs>


    <MessageListForStudent  ref="messageList"></MessageListForStudent>
  </div>
</template>

<script>
  import MessageListForStudent from './MessageListForStudent'
  import storage from '../utils/storage'

  export default {
    components: {
      MessageListForStudent
    },
    data() {
      return {
        activeName: 'first',
        pageSizeTeacher:10,
        currentPageTeacher:1,
        pageTotalTeacher:0,
        pageSizeSchool:10,
        currentPageSchool:1,
        pageTotalSchool:0,
        pageSizeStudent:10,
        currentPageStudent:1,
        pageTotalStudent:0,
        teacherData:[],
        schoolData:[],
        studentData:[],
        show_num:0,
        token:'',
        userInfo:[]
      }
    },
    methods: {
      handleSizeChangeTeacher:function(val) {
        this.pageSizeTeacher = val;
        this.getTeacherData();
      },
      handleCurrentChangeTeacher:function(val) {
        this.currentPageTeacher = val;
        this.getTeacherData();
      },


      handleClick(tab, event) {
        console.log(tab, event);
      },

      openMessageList(){
        var obj={
          send_type:'student',
          is_teacher_admin:false
        }
        this.$refs.messageList.initForm(obj)
      },

      getTeacherData(){
        var param = {
          pageSize: this.pageSizeTeacher,
          page: this.currentPageTeacher,
        };
        var url=this.baseUrl+"/api/getTeachersDataByStudentId"
        // var url = "http://www.myteach.com/api/getTeacherData";
        this.axios.get(url, param).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.pageSizeTeacher=res.data.data.pageSize
            this.pageTotalTeacher = res.data.data.total
            this.teacherData=res.data.data.data
            this.teacherData.forEach(value => {
              if(value.follow.length>0){
                value.is_follow='是'
              }else{
                value.is_follow='否'
              }
            })
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      follow(row,status){
        var url=this.baseUrl+"/api/follow"
        this.axios.post(url, {status:status,user_id:row.id}).then((res)=> {
          console.log(res)
          if (res.data.code != 200) {

            return false;
          }else{
            this.$message({
              message: res.data.msg,
              type: "success"
            });
             this.getTeacherData()
          }
        }).catch((error)=> {
          console.log(error);
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      websocketMessage(){
        setTimeout(()=>{
          const  userInfo=storage.get('userinfo')
          //创建一个webSocket 实例
          // var s=location.origin
          // var h=s.substr(0,s.lastIndexOf(":"))
          //var host= location.origin.replace(/^http/, 'ws')
          //   var host= 'wss://myteachceshi.herokuapp.com'
          var host ='ws://127.0.0.1:9000'
          var ws = new WebSocket(host);
          var uid=userInfo.id
          ws.onopen = function(){

            var params ={
              uid:uid,
              type:'student'
            };
            var data=JSON.stringify(params)
            ws.send(data);
          };
          ws.onmessage = (e)=>{
            var res=JSON.parse(e.data);
            console.log(res.data.show_num);
            this.show_num=res.data.show_num
          };
        },300)




      }
    },

    mounted() {
      this.getTeacherData()
      this.getSchoolData()
      this.getStudentData()

      this.websocketMessage()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



